import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Food = ({ item, editable, handleProtOnChange }) => {
  const [_item, setItem] = useState(item);
  const [cals, setCals] = useState();
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
    calulateCalories();
  }, [editable]);

  const calulateCalories = () => {
    setCals(Math.round((_item.carb + _item.prot) * 4 + _item.fat * 9));
  };

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  const _handleProtOnChange = value => {
    setProt(Number(value));
    calulateCalories();
  };

  return (
    <tr>
      <th scope="row">{_item.id}</th>
      <td>{isEditable ? <Input type="text" value={_item.name} /> : _item.name}</td>
      <td>{isEditable ? <Input type="text" value={_item.group} /> : _item.group}</td>
      <td>
        {isEditable ? (
          <Input type="number" value={_item.portion} />
        ) : (
          _item.portion + ' ' + _item.units
        )}
      </td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_item.prot}
            onChange={event => handleProtOnChange({ ..._item, prot: event.target.value })}
          />
        ) : (
          _prot + ' g'
        )}
      </td>
      <td>{isEditable ? <Input type="number" value={_item.carb} /> : _item.carb + ' g'}</td>
      <td>{isEditable ? <Input type="number" value={_item.fat} /> : _item.fat + ' g'}</td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
